import React from 'react'
import { Link } from 'gatsby'
import styles from './header.module.css'

import { compose, withStateHandlers } from 'recompose'

// import style from './langLink.module.css'
import { css } from 'utils'
import { FormattedMessage } from 'react-intl'

const LinkLang = ({ selectLanguage, lang }) => (
  <li key={Math.random()}>
    <Link
      // to={global.window.location.pathname} // dont work in build
      to={""}

      onClick={e => {
        switch (lang) {
          case 'it':
            selectLanguage('de')
            // console.log('toggle to de')

            break
          case 'de':
            selectLanguage('it')
            // console.log('toggle to en')

            break

          default:
            selectLanguage('it')

            break
        }
      }}
    >
      <FormattedMessage id={'switchLanguage'} />
    </Link>
  </li>
)

const enhance = compose(
  withStateHandlers(props => ({ language: props.lang }), {
    selectLanguage: (values, { toggleLanguage }) => value => {
      toggleLanguage(value)
      return {
        language: value,
      }
    },
  })
)

export default enhance(LinkLang)
